<template>
    <div class="container-fluid">
        <h2>Deck types update</h2>

    </div>
</template>


<script>
  import { mapGetters, mapActions } from 'vuex'
  import * as storeMut from '@/store/mutation-types'

  export default {
    components: {},
    data () {
      return {
        loadedDeckTypes: [],
        sortBy: 'star',
        topFirst: true
      }
    },
    computed: {
      ...mapGetters(['loadedDeckTypeUpdate'])
    },
    methods: {
      ...mapActions(['loadDeckTypeUpdate']),
      remove (id) {
        this.$store.commit(storeMut.REMOVE_DECKTYPE, id)
      }
    },
    mounted () {
      this.loadDeckTypeUpdate().then(
        (result) => {
          this.loadedDeckTypes = result
        }
      ).catch(() => {})
    }
  }
</script>


<style>
</style>



